@value popupBackground: #fff;
@value popupBorderColor: transparent;
:local .popup {
  position: absolute;
  min-width: 18px;
  background: #fff;
  border-radius: 2px;
  border: none transparent;
}
:local :global(.rt-ie-any) .shadow {
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.2);
}
:local :global(.rt-ie8) .popup {
  border: 1px solid #d0d0d0;
}
:local .shadow {
  filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.1)) drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
  -webkit-filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.1)) drop-shadow(0 1px 2px rgba(0, 0, 0, 0.3));
}
:local .transition-enter {
  opacity: 0.01;
}
:local .transition-enter-top {
  transform: translateY(10px);
}
:local .transition-enter-bottom {
  transform: translateY(-10px);
}
:local .transition-enter-left {
  transform: translateX(10px);
}
:local .transition-enter-right {
  transform: translateX(-10px);
}
:local .transition-enter-active {
  transition: transform 0.18s cubic-bezier(0.22, 0.61, 0.36, 1), opacity 0.18s cubic-bezier(0.22, 0.61, 0.36, 1);
  opacity: 1;
  transform: translate(0, 0);
}
:local .transition-exit {
  opacity: 0.01;
  transition: opacity 0.15s ease-out;
  transform: translate(0, 0);
}
