:local .wrap {
  box-sizing: border-box;
  display: inline-block;
  padding: 0;
}
:local .root {
  border: 0;
  color: #404040;
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  font-size: 14px;
  margin: 0;
  outline: 0;
  overflow: visible;
  position: relative;
  width: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-clip: border-box;
}
:local .root::-moz-focus-inner {
  border: 0;
  padding: 0;
}
:local .root:active {
  padding-top: 1px;
  vertical-align: -1px;
}
:local .root:after {
  content: '';
  display: inline-block;
  vertical-align: baseline;
  width: 0;
}
:local .root .warning,
:local .root .error {
  border-radius: inherit;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
:local .root:not(.link) .warning {
  box-shadow: 0 0 0 2px #f69c00;
}
:global(.rt-ie8) :local .root:not(.link) .warning {
  outline: 2px solid #f69c00;
}
:local .root:not(.link) .error {
  box-shadow: 0 0 0 2px #d70c17;
}
:global(.rt-ie8) :local .root:not(.link) .error {
  outline: 2px solid #d70c17;
}
:local .caption {
  position: relative;
  white-space: nowrap;
  display: inline-block;
  width: 100%;
}
:local .icon {
  display: inline-block;
  padding-right: 7px;
}
:local .sizeSmall {
  line-height: 32px;
  padding: 0 15px;
}
:local .sizeSmall:not(.link) {
  height: 34px;
}
:local .DEPRECATED_sizeMedium,
:local .sizeMedium {
  font-size: 16px;
  line-height: 38px;
  padding: 0 15px;
}
:local .DEPRECATED_sizeMedium:not(.link),
:local .sizeMedium:not(.link) {
  height: 40px;
}
:local .sizeLarge {
  font-size: 16px;
  line-height: 42px;
  padding: 0 20px;
}
:local .sizeLarge:not(.link) {
  height: 44px;
}
:local .buttonWithIcon {
  padding-right: 15px;
  padding-left: 15px;
}
:local .default {
  background: none;
  color: none;
  border: 1px solid rgba(0, 0, 0, 0.15);
}
:local .default:hover {
  background: rgba(0, 0, 0, 0.06);
}
:local .default.focus {
  box-shadow: inset 0 0 0 1px #fff, 0 0 0 1px #1d85d0;
  border: 1px solid #1d85d0;
  position: relative;
  z-index: 2;
}
:local .default:active,
:local .default.active {
  background: rgba(0, 0, 0, 0.12);
}
:local .primary {
  background: #1d85d0;
  color: #fff;
  border: 1px solid transparent;
}
:local .primary:hover {
  background: #1b7dc3;
}
:local .primary.focus {
  box-shadow: inset 0 0 0 1px #fff, 0 0 0 1px #1d85d0;
  border: 1px solid #1d85d0;
  position: relative;
  z-index: 2;
}
:local .primary:active,
:local .primary.active {
  background: #1974b6;
}
:local .success {
  background: #419d14;
  color: #fff;
  border: 1px solid transparent;
}
:local .success:hover {
  background: #3c9312;
}
:local .success.focus {
  box-shadow: inset 0 0 0 1px #fff, 0 0 0 1px #1d85d0;
  border: 1px solid #1d85d0;
  position: relative;
  z-index: 2;
}
:local .success:active,
:local .success.active {
  background: #398911;
}
:local .danger {
  background: #d9472b;
  color: #fff;
  border: 1px solid transparent;
}
:local .danger:hover {
  background: #cc4228;
}
:local .danger.focus {
  box-shadow: inset 0 0 0 1px #fff, 0 0 0 1px #1d85d0;
  border: 1px solid #1d85d0;
  position: relative;
  z-index: 2;
}
:local .danger:active,
:local .danger.active {
  background: #be3e25;
}
:local .pay {
  background: #ffca43;
  color: none;
  border: 1px solid transparent;
}
:local .pay:hover {
  background: #f0be3f;
}
:local .pay.focus {
  box-shadow: inset 0 0 0 1px #fff, 0 0 0 1px #1d85d0;
  border: 1px solid #1d85d0;
  position: relative;
  z-index: 2;
}
:local .pay:active,
:local .pay.active {
  background: #e0b13a;
}
:local .root.link {
  display: inline;
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  box-shadow: none;
  line-height: inherit;
}
:local .root.link .caption {
  display: inline;
}
:local .root.link:active {
  vertical-align: 0;
}
:local .root.link.disabled {
  box-shadow: none;
  background: none;
}
:local .root.link .icon {
  padding-right: 3px;
}
:local .root.link.linkError .caption {
  background: #ffd6d6;
}
:local .root.link .warning,
:local .root.link .error {
  left: -2px;
  right: -2px;
  bottom: -2px;
}
:local .root.link .error {
  background: #ffd6d6;
}
:local .link {
  border-radius: 1px;
  outline: none;
  text-decoration: none;
  color: #3072c4;
}
:local .link:hover {
  color: #3072c4;
}
:local .link.focus {
  color: #3072c4;
}
:local .link:active {
  color: #044785;
}
:local .link:hover,
:local .link.focus {
  text-decoration: underline;
}
:local .link.disabled {
  box-shadow: none;
  color: #a0a0a0;
  cursor: default;
  text-decoration: none;
}
:local .root.checked {
  background: #7e7e7e;
  color: #fff;
  padding-top: 1px;
}
:local .root.checked:hover {
  background: #7e7e7e;
}
:local .root.checked.focus {
  box-shadow: inset 0 0 0 1px #fff, 0 0 0 2px #5199db;
  position: relative;
  z-index: 2;
}
:local .root.disabled {
  background: #f2f2f2;
  border-color: transparent;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15);
  color: #808080;
  cursor: default;
}
:local .root.disabled:active {
  padding-top: 0;
  vertical-align: 0;
}
:local .loading {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: inherit;
  overflow: hidden;
}
:local .loading:before {
  content: '';
  height: 100%;
  position: absolute;
  opacity: 0.2;
  background: linear-gradient(-110deg, #ccc 30%, transparent 0, transparent 60%, #ccc 0);
  background-size: 30px 100%;
  top: 0;
  left: 0;
  right: -30px;
  transform: rotateY(180deg) skewX(0deg) skewY(0deg);
}
:local .loading:before :global {
  animation: btn_loading 1s linear infinite;
}
:local .narrow {
  padding-left: 5px;
  padding-right: 5px;
}
:local .noPadding {
  padding-left: 0;
  padding-right: 0;
}
:local .noRightPadding {
  padding-right: 0;
}
:local .arrow {
  content: '';
}
:local .arrow_loading {
  content: '';
}
:local .arrow_warning {
  content: '';
}
:local .arrow_error {
  content: '';
}
:local .wrap_arrow {
  content: '';
}
:local .borderless {
  box-shadow: none;
}
:local .borderless:hover,
:local .borderless:active {
  box-shadow: none;
}
@keyframes btn_loading {
  0% {
    transform: translateX(0) rotateY(180deg);
  }
  100% {
    transform: translateX(-30px) rotateY(180deg);
  }
}
@keyframes btn_loading_arrow {
  0% {
    transform: translateX(50px) rotate(-44.3deg) skewX(0deg) skewY(0deg);
  }
  100% {
    transform: translateX(21px) translateY(30px) rotate(-44.3deg) skewX(0deg) skewY(0deg);
  }
}
