:local .root {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
}
:local .bg {
  background: #333;
  bottom: 0;
  left: 0;
  opacity: 0.6;
  position: absolute;
  right: 0;
  top: 0;
}
:local .container {
  position: relative;
  box-sizing: border-box;
  white-space: nowrap;
  text-align: center;
  height: 100%;
  overflow-y: auto;
  padding: 20px 0;
  outline: none;
}
:local .container:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  height: 80%;
}
@media screen and (max-width: 425px) {
  :local .container.mobile {
    padding: 0;
  }
  :local .container.mobile .centerContainer {
    margin: 0;
    width: 100%;
  }
}
:local .centerContainer {
  position: relative;
  display: inline-block;
  margin: 0 20px;
  text-align: left;
  vertical-align: middle;
  box-sizing: border-box;
  z-index: 10;
}
:local .centerContainer.alignTop {
  vertical-align: top;
}
:local .window {
  position: relative;
  white-space: normal;
  margin: auto;
  background: #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
:local .close {
  position: relative;
  float: right;
  margin: 2px 2px 0 0;
  width: 76px;
  box-sizing: border-box;
  height: 65px;
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
}
:local .close.disabled {
  color: #8b8b8b;
  pointer-events: none;
  cursor: default;
}
:local .close:after,
:local .close:before {
  content: '';
  position: absolute;
  top: 30px;
  right: 40px;
  height: 18px;
  width: 2px;
  background: #808080;
}
:local .close:after {
  transform: rotate(45deg);
}
:local .close:before {
  transform: rotate(-45deg);
}
:local .close:focus:before,
:local .close:focus:after,
:local .close:hover:before,
:local .close:hover:after {
  background: #333;
}
:local .close .closeOutline {
  display: none;
}
:local .close:focus .closeOutline {
  position: absolute;
  display: block;
  top: 24px;
  right: 26px;
  width: 26px;
  height: 26px;
  border: 2px solid #5199db;
}
:local .absoluteClose {
  position: absolute;
  right: 0;
  top: 0;
}
:local .header {
  font-size: 22px;
  line-height: 30px;
  padding: 24px 110px 11px 30px;
}
:local .fixedHeader,
:local .fixedFooter {
  background: #fff;
}
:local .fixedHeader {
  margin-bottom: 10px;
}
:local .fixedHeader:after,
:local .fixedFooter:before {
  content: '';
  position: absolute;
  height: 1px;
  width: 100%;
  left: 0;
  z-index: -1;
}
:local .fixedHeader:after {
  bottom: 11px;
  box-shadow: 0 1px 10px #000000;
}
:local .headerAddPadding {
  padding-bottom: 22px;
}
:local .body {
  padding: 0 35px 25px 30px;
}
:local .bodyWithoutHeader {
  padding-top: 30px;
}
:local .bodyAddPadding {
  padding-bottom: 30px;
}
:local .footer {
  padding: 0 35px 30px 30px;
}
:local .footer.panel {
  background: #e9e9e9;
  padding-top: 20px;
  padding-bottom: 20px;
}
:local .fixedFooter {
  padding-top: 20px;
  margin-top: 10px;
}
:local .fixedFooter:before {
  top: 11px;
  box-shadow: 0 -1px 10px #000000;
}
:local .modalClickTrap {
  position: absolute;
  padding: 20px 0;
  box-sizing: content-box;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
}
